<template>
  <div class="quarters">
    <!--  -->
    <el-form :inline="true" :model="quarter" class="demo-form-inline">
      <el-form-item label="">
        <el-input v-model="quarter.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="quarter.createTime"
          type="daterange"
          range-separator=""
          start-placeholder=""
          end-placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search"></el-button>
      </el-form-item>
    </el-form>
        <!--  -->
    <div>
      <el-button type="primary" @click="addQuarter"></el-button>
    </div>
    <!--  -->
    <el-table
      :data="tableData"
      border
      style="width: 100%;margin-top: 25px;">
      <el-table-column
        prop="communityId"
        label-class-name="etc"
        label="ID"
        width="100">
      </el-table-column>
      <el-table-column
        prop="communityName"
        label-class-name="etc"
        label=""
        >
      </el-table-column>
      <el-table-column
        prop="communityAddress"
        label-class-name="etc"
        label="">
      </el-table-column>
      <el-table-column
        prop="communityUserCount"
        label-class-name="etc"
        label="">
      </el-table-column>
      <el-table-column
        prop="communityShopCount"
        label-class-name="etc"
        label="">
      </el-table-column>
      <el-table-column
        prop="createTime"
        label-class-name="etc"
        label="">
      </el-table-column>
      <el-table-column
        label=""
        label-class-name="etc"
        width="200">
        <template slot-scope="scope">
          <el-button @click="showStores(scope.row)" type="text" size="small"></el-button>
          <el-button @click="editQuarter(scope.row)" type="text" size="small"></el-button>
          <el-button type="text" size="small" @click="del(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
     <!--  totaldataCount -->
    <el-pagination
      small
      layout="prev, pager, next"
      :total="50" 
      @current-change="changePage"
      
      
      >
    </el-pagination>
    <!--  -->
    <el-dialog :title="title?'':''" :visible.sync="show">
      <el-form :model="items">
        <el-form-item label="" label-width="100px">
          <el-input v-model="items.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <!--  -->
          <el-select v-model="items.province" placeholder="" @change="changeProvince">
            <el-option
              v-for="item in areas"
              :key="item.id"
              :label="item.cityName"
              :value="item.cityName">
            </el-option>
          </el-select>
          <!--  -->
          <el-select v-model="items.citys" placeholder="" @change="changeCity">
            <el-option
              v-for="item in city"
              :key="item.id"
              :label="item.cityName"
              :value="item.cityName">
            </el-option>
          </el-select>
          <!--  -->
          <el-select v-model="items.regions" placeholder="">
            <el-option
              v-for="item in region"
              :key="item.id"
              :label="item.cityName"
              :value="item.cityName">
            </el-option>
          </el-select>
          <!--  -->
          <el-input v-model="items.adress" autocomplete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <el-button @click="getPosition"></el-button>
          {{ items.position.length > 0 ?items.position.join(','):'' }}
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="show = false"> </el-button>
        <el-button type="primary" @click="show = false;addQuarterRow();addEditQuarter()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios';
// 
import area from '../utils/area.json'

export default {
  name:'Quarters',
  data() {
    return {
      // 
      quarter: {
        name: '', // 
        createTime:[] // 
        
      },
      tableData:[],
      dataCount:0, // 
      currentPage:1, // 
      startTime:'', // 
      endTime:'',  //  
      show:false, //  
      items:{
        name:'', // 
        citys:'', // 
        province:'', // 
        regions:'', // 
        adress:'', // 
        position:[] // 
      },
      areas:area, //
      city:'' ,   // 
      region:'',   // 
      title:false, // 
      obj:{}  // 
    }
  },
  methods: {
    // 
    async getDate(){
      let res = await axios.get(`http://43.143.190.87:8091/panghudaojiaBackend/community/communityList?name=&startTime=${this.startTime}&endTime=${this.endTime}&pageNum=${this.currentPage}&pageSize=10&communityName=`+this.quarter.name)
      // console.log(res.data.data);
      this.tableData = res.data.data.list
      this.dataCount = res.data.data.total
      // console.log(res);
      
    },
    search(){
      // console.log(1);
      
      console.log(this.quarter);
      //   name 
      if(this.quarter.name.trim() == '' && !this.quarter.createTime){
        this.$message({
          message:'',
          type:'warning',
          duration:1000
        })
        return
      }
      // 
      // if(){
      //   this.$message({
      //     message:'',
      //     type:'warning',
      //     duration:1000
      //   })
      //   return
      // }
      // console.log(this.quarter.createTime);
      
      if(this.quarter.createTime){
        // 
        let startTime = this.quarter.createTime[0]
        let endTime = this.quarter.createTime[1]
        //  -- ::

        var y = startTime.getFullYear();   // 
        var m = startTime.getMonth() + 1;  // 
        m = m < 10 ? ('0' + m) : m;        // 100
        var d = startTime.getDate();       // 
        d = d < 10 ? ('0' + d) : d;        // 100
        var h = startTime.getHours();      // 
        h = h < 10 ? ('0' + h) : h;
        var minute = startTime.getMinutes(); // 
        minute = minute < 10 ? ('0' + minute) : minute; // 100
        startTime = y + '-' + m + '-' + d+'+'+h+':'+minute+":00"; 


        var y = endTime.getFullYear();
        var m = endTime.getMonth() + 1;
        m = m < 10 ? ('0' + m) : m;
        var d = endTime.getDate();
        d = d < 10 ? ('0' + d) : d;
        var h = endTime.getHours();
        h = h < 10 ? ('0' + h) : h;
        var minute = endTime.getMinutes();
        minute = minute < 10 ? ('0' + minute) : minute;
        endTime = y + '-' + m + '-' + d+'+'+h+':'+minute+":00"; 

        // console.log(startTime, endTime);

        this.startTime = startTime
        this.endTime = endTime
      }else{
        // 
        this.startTime = ''
        this.endTime = ''
      }
      
      // 
      this.getDate()

    },
    addQuarter(){
      this.show = true

    },
    // 
    async addQuarterRow(){
      // console.log(1);
      if(!this.title){
        if(!this.items.citys ||!this.items.province||!this.items.regions || !this.items.adress || !this.items.position ){
        this.$message({
          message:'',
          type:'warning',
          duration:1000
        })
        this.show = true
        return
      }
      this.obj = {
        addOrUpdateFlag:1,
        communityAddress:this.items.adress,
        communityAreaCode:'',
        communityCity:this.items.citys,
        communityCoordinates:this.items.province+this.items.citys+this.items.regions,
        communityDistrict:this.items.regions,
        communityId:'',
        communityLatitude:this.items.position.length>0 ? this.items.position[0]:'',
        communityLongitude:this.items.position.length>0 ? this.items.position[1]:'',
        communityName:this.items.name,
        communityProvince:this.items.province
      }
      let res = await axios.post(`http://43.143.190.87:8091/panghudaojiaBackend/community/communityUpdate`,this.obj)
      // console.log(res);

      if(res.data.status == 200){
        this.$message({
          message:'',
          type:'success',
          duration:1000,
          onClose:() => {
            this.show = false
            this.getDate()
            // console.log(this.tableData);
            
          }
        })
        
      }
      }

      
      
    },
    showStores(data){

    },
    editQuarter(data){
      this.show = true
      this.title = true
      // console.log(data);

      // 
      this.items.name = data.communityName
      this.items.province = data.communityProvince
      this.items.citys = data.communityCity
      this.items.regions = data.communityDistrict
      this.items.adress = data.communityAddress
      this.items.position[0] = data.communityLatitude
      this.items.position[1] = data.communityLongitude

      console.log(this.items.name);
      
    },
    addEditQuarter(){
      // this.obj = {
      //   addOrUpdateFlag:1,
      //   communityAddress:this.items.adress,
      //   communityAreaCode:'',
      //   communityCity:this.items.citys,
      //   communityCoordinates:this.items.province+this.items.citys+this.items.regions,
      //   communityDistrict:this.items.regions,
      //   communityId:'',
      //   communityLatitude:this.items.position.length>0 ? this.items.position[0]:'',
      //   communityLongitude:this.items.position.length>0 ? this.items.position[1]:'',
      //   communityName:this.items.name,
      //   communityProvince:this.items.province
      // }
      if(this.title){
        data.communityName =  this.items.name
        data.communityProvince=  this.items.province 
        data.communityCity = this.items.citys  
        data.communityDistrict= this.items.regions 
        data.communityAddress = this.items.adress 
        data.communityLatitude=this.items.position[0] 
        data.communityLongitude= this.items.position[1] 
      }
      
      
      
      // console.log(this.items.name);
      
    },
    del(data){
      // console.log(data);
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        // 
        let res = await axios.post('http://43.143.190.87:8091/panghudaojiaBackend/community/communityRemove',{"communityId": data.communityId}
        )
        
        
        // console.log(res);
        // 
        if(res.status == 200){
          this.$message({
            type:'success',
            message:'',
            duration:1000,
            onClose:() => {
              // 
              if(this.tableData.length == 1){
                this.currentPage--
              }
              this.getDate()
              // console.log(this.tableData);
              
              // if(this.tableData.length === 0){
              //   this.currentPage--
              //   this.getDate()
              // }
              // bug
              // 4.5 
            }
            // onClose(){
            //   this.getDate()
            // }
          })
        }else{
          this.$message({
            type:'error',
            message:''
          })
        }
        
        this.$message({
          type: 'success',
          message: '!'
        });
      }).catch(() => {
              
      });

    },
    changePage(pageNum){
      // 
      this.currentPage = pageNum
      this.getDate()
    },
    // 
    changeProvince(val){
      // console.log(val);
      for(let i = 0; i < this.areas.length;i++){
        if(this.areas[i].cityName == val){
          this.city = this.areas[i].provincesCityList
          break
        }
      }
      this.items.citys = ''
      this.items.regions = ''
      this.region = []
      
      // console.log(this.city);
      
    },
    // 

    changeCity(val){
      for(let i = 0; i < this.city.length;i++){
        if(this.city[i].cityName == val){
          this.region = this.city[i].provincesCityList
          break
        }
      }
      // console.log(this.items.citys);
      this.items.regions = ''

      
    },
    // 
    async getPosition(){
      if(!this.items.province){
        this.$message({
          message:'',
          type:'warning',
          duration:1000
        })
        return
      }
      if(!this.items.citys){
        this.$message({
          message:'',
          type:'warning',
          duration:1000
        })
        return
      }
      if(!this.items.regions){
        this.$message({
          message:'',
          type:'warning',
          duration:1000
        })
        return
      }
      if(!this.items.adress){
        this.$message({
          message:'',
          type:'warning',
          duration:1000
        })
        return
      }
      let address = this.items.province+this.items.citys+this.items.regions+this.items.adress
      
      let res = await axios.get(`https://restapi.amap.com/v3/geocode/geo?key=457886b5972a97e93fb9cdccebfd450f&address=${address}`)
      // console.log(res);
      let location = res.data.geocodes[0].location
      this.items.position = location.split(',').reverse()
      // console.log(this.items.position);
      
      
    }
    
    
  },
  mounted() {
    // 
    this.getDate()
    
  },
}
</script>

<style>
.el-table__cell{
  text-align: center !important;
}
th.el-table__cell{
  background-color: #1890ff !important;
  color: #fff;
}
.el-select{
  width: 190px !important;
  margin-right: 15px;
}
.el-dialog .el-input__inner{
  margin-top: 15px !important;
}
</style>